import  '../styles/SectionCard.scss'

const SectionCard = (props) => {
    const paragraphs = props.cardContent.paragraphs.map( paragraph => {
        return <div>{paragraph}</div>
    })
    return <section className = 'section-card' id = {props.id}>
                <img className = 'section-card__image' src = {props.cardContent.pictureUrl}/>
                <h2 className = 'section-card__title'>{props.cardContent.title}</h2>
                <div className = 'section-card__text-container' style={props.style}>
                    
                   
                    <h2 className = 'section-card__subtitle'>
                            {props.cardContent.subtitle}   
                    </h2>

                            <i className={props.fontAwesomeIcons[0].className} style = {{'color': props.fontAwesomeIcons[0].color}}></i>
                            <i className={props.fontAwesomeIcons[1].className} style = {{'color': props.fontAwesomeIcons[1].color}}></i>
                            <i className={props.fontAwesomeIcons[2].className} style = {{'color': props.fontAwesomeIcons[2].color}}></i>
                            
                    <div className = 'section-card__content'>
                        {paragraphs}
                    </div>
                </div>
            </section>
}

export default SectionCard

